/* ---------------------------------------------------------------Animation  */
#header .img-fluid{
animation: up-down 2s ease-in-out infinite alternate-reverse both !important;
}
@-webkit-keyframes up-down{
    0%{
        transform: translate(10x);
    }
    100%{
        transform: translate(-10px);
    }
}
@keyframesup-down{
    0%{
        transform: translate(10x);
    }
    100%{
        transform: translate(-10px);
    }
}
